.project-wrap {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  width: 80%;
  margin: auto;
  background-color: white;
  color: #333;
  margin-top: 2em;
  flex-direction: column;
  max-width: 1200px; }
  @media (min-width: 940px) {
    .project-wrap {
      flex-direction: row;
      padding: 1.5em 4em; } }

.project-title {
  margin-top: 0;
  text-align: center; }
  @media (min-width: 940px) {
    .project-title {
      text-align: left; } }

.project-flex-technologies {
  display: flex;
  justify-content: flex-start; }
  .project-flex-technologies h3 {
    margin-right: 1em; }

.project-right-wrap {
  width: 100%; }
  @media (min-width: 940px) {
    .project-right-wrap {
      width: 55%; } }

.project-description-text {
  font-size: 1.15em; }

.project-button-wrap {
  margin-top: 1.5em; }
  @media (min-width: 940px) {
    .project-button-wrap {
      display: flex;
      justify-content: flex-end; } }
  .project-button-wrap button {
    width: 100%;
    padding: .5em;
    background-color: white;
    font-size: 1.15em;
    cursor: pointer;
    border: 2px solid #45B8D8;
    color: #45B8D8;
    outline: none;
    margin-top: 1em; }
    @media (min-width: 940px) {
      .project-button-wrap button {
        margin-top: 0;
        width: 200px;
        margin-left: 1em; } }
    .project-button-wrap button:hover {
      background-color: #45B8D8;
      color: white; }
