.contact-wrap {
  margin: auto;
  display: block;
  padding: 0 2em; }
  @media (min-width: 940px) {
    .contact-wrap {
      width: 750px; } }

.contact-row {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 940px) {
    .contact-row {
      width: 750px;
      flex-direction: row;
      align-items: flex-start; } }

.contact-textarea {
  width: 100%;
  height: 250px;
  box-sizing: border-box; }

.contact-button {
  width: 200px;
  padding: .5em;
  background-color: white;
  font-size: 1.15em;
  cursor: pointer;
  border: 2px solid #45B8D8;
  color: #45B8D8;
  margin: auto;
  margin-top: 1em;
  margin-bottom: 2em;
  display: block;
  outline: none; }
  .contact-button:hover {
    background-color: #45B8D8;
    color: white; }
